import "./consiglio.js";

$(document).ready(function () {
    var url_to_call = apibase.baseurl + "/oi/10400"

    $.ajax({
        url: url_to_call, success: function (result_api) {
            if (result_api != undefined) {
                let organismi = result_api.content;
                if (organismi != null) {
                    for (var i = 0; i < organismi.length; i++) {
                        let singleOrg = organismi[i];
                        let urlDettaglio = "pagina-organismo.html?id=" + singleOrg.id;
                        let thumbImb = "";
                        if (singleOrg.components != undefined && singleOrg.components.banner != undefined && singleOrg.components.banner.components != undefined) {
                            let img = singleOrg.components.banner.components[0];
                            if (img != undefined) {
                                var thumb = baseImgUrl + img.properties.thumbaddress;
                                // var thumb = "http://localhost:8080/assets/consiglioinformadiretta.png";
                                thumbImb = '<img src="' + thumb + '" width="64" height="64" class="bg-dark-blue mr-2 mb-1 float-left" />';
                            }
                        }
                        var boxHtml = "<a class='dark-obj-name-bold' href='" + urlDettaglio + "'><h4> " + singleOrg.properties.title + "</h4></a>" + "<div class='box-organismo mb-4'>" + thumbImb + singleOrg.properties.sommario + "</div>"

                        if (i % 2 == 0) {
                            $("#colonnaDispari").append(boxHtml);
                        } else {
                            $("#colonnaPari").append(boxHtml);
                        }
                    }
                }
            } else {
                window.location = 'notFound.hml';
            }
        }
    });
});